import React from 'react'
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { Link, Navigate, useLocation } from 'react-router-dom';
import { loginUserAsync, selectError, selectLoggedInUser } from '../authSlice';
import axios from 'axios';
import '../../../App.css';

const UserOtp = () => {
    const dispatch = useDispatch();
  const error = useSelector(selectError);
  const user = useSelector(selectLoggedInUser);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const useQuery = () => {
    return new URLSearchParams(useLocation().search);
  };

  const query = useQuery();
  const email = query.get('email');

  const handleButton = () => {
    // Initiate Email
    axios.get(`https://tpfshophub.onrender.com/auth/otp-verification?email=${email}`)
    .then(users => console.log(users))
    .catch(err => console.log(err))
  }

  return (
    <>
      {user && <Navigate to="/home" replace={true} />}
      <div className="flex min-h-screen flex-col justify-center px-6 py-12 lg:px-8" style={{ backgroundColor: "#220043" }}>
        <div className="sm:mx-auto sm:w-full sm:max-w-md">
          <img
            className="mx-auto h-40 w-auto"
            src="/ecommerce.png"
            alt="Your Company"
          />
          <h1 className="text-center text-lg font-bold mt-0 shimmer-text">
            The exclusive merchandise platform for associates
          </h1>
          <h2 className="mt-6 text-center text-2xl font-bold leading-9 tracking-tight text-gray-300">
            Login
          </h2>
        </div>

        <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
          <form
            noValidate
            onSubmit={handleSubmit((data) => {
              dispatch(
                loginUserAsync({ email: email, password: data.password })
              );
            })}
            className="space-y-4 w-full"  // Reduced space here
          >
            <div>
              <label
                htmlFor="email"
                className="block text-sm font-medium leading-6 text-gray-200"
              >
                Enter OTP
              </label>
              <div className="mt-2">
                <input
                  id="password"
                  {...register('password', {
                    required: 'OTP is required',
                  })}
                  type="password"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                {errors.password && (
                  <p className="text-red-500">{errors.password.message}</p>
                )}
              </div>
              {error && <p className="text-red-500">{error || error.message}</p>}
            </div>

            <div>
              <button
                type="submit"
                className="flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Verify
              </button>
            </div>
          </form>

          <div className="mt-2 text-sm flex justify-between">
            <Link
              to="/login"
              className="font-semibold text-indigo-600 hover:text-indigo-500"
            >
              Back
            </Link>
            <button
              onClick={handleButton}
              className="font-semibold text-indigo-600 hover:text-indigo-500 bg-transparent border-none cursor-pointer p-0"
              style={{ textDecoration: 'none' }}
            >
              Resend OTP
            </button>
          </div>
        </div>
      </div>
    </>
  )
}

export default UserOtp
