import { useSelector, useDispatch } from 'react-redux';
import { selectError, selectLoggedInUser } from '../authSlice';
import { Link, Navigate, useNavigate } from 'react-router-dom';
import { loginUserAsync } from '../authSlice';
import { useForm } from 'react-hook-form';
import { useState } from 'react';
import axios from 'axios';
import '../../../App.css'

export default function Login() {
  const dispatch = useDispatch();
  const error = useSelector(selectError);
  const user = useSelector(selectLoggedInUser);
  
  const [val, setVal] = useState(" ");

  const history = useNavigate();

  const change = event => {
    const newValue = event.target.value;
    setVal(newValue); 
  }

  const handleButton = () => {
    console.log(val); 
    history(`/otp-verification?email=${encodeURIComponent(val)}`);

    // Initiate Email
    axios.get(`https://tpfshophub.onrender.com/auth/otp-verification?email=${encodeURIComponent(val)}`)
    .then(users => console.log(users))
    .catch(err => console.log(err))

  }

  return (
    <>
      {user && <Navigate to={`/otp-verification`} replace={true} />}
      <div className="flex min-h-screen flex-col justify-center px-6 py-12 lg:px-8">
        <div className="sm:mx-auto sm:w-full sm:max-w-md">
          <img
            className="mx-auto h-40 w-auto"
            src="/ecommerce.png"
            alt="Your Company"
          />
          <h1 className="text-center text-lg font-bold mt-0 shimmer-text">
            The exclusive merchandise platform for associates
          </h1>
          <h2 className="mt-6 text-center text-2xl font-bold leading-9 tracking-tight text-gray-300">
            Login
          </h2>
        </div>

        <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
          <div>
            <label
              htmlFor="email"
              className="block text-sm font-medium leading-6 text-gray-200"
            >
              Email address
            </label>
            <div className="mt-2">
              <input
                id="email"
                type="email"
                value={val}
                onChange={change}
                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
            </div>

            <div>
              <button onClick={handleButton}
                type="button"
                className="flex w-full justify-center rounded-md bg-indigo-600 mt-3 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Get OTP
              </button>
            </div>             
          </div>
          {/* <div className="mt-10 text-center text-sm text-gray-500">
            
          </div> */}
        </div>
      </div>
    </>
  );
}
