import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { selectUserInfo, updateUserAsync } from '../userSlice';
import { useForm } from 'react-hook-form';

export default function UserProfile() {
  const dispatch = useDispatch();
  const userInfo = useSelector(selectUserInfo);
  const [selectedEditIndex, setSelectedEditIndex] = useState(-1);
  const [showAddAddressForm, setShowAddAddressForm] = useState(false);

  const {
    register,
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm();

  const handleEdit = (addressUpdate, index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] };
    newUser.addresses.splice(index, 1, addressUpdate);
    dispatch(updateUserAsync(newUser));
    setSelectedEditIndex(-1);
  };

  const handleRemove = (e, index) => {
    e.preventDefault();
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] };
    newUser.addresses.splice(index, 1);
    dispatch(updateUserAsync(newUser));
  };

  const handleEditForm = (index) => {
    setSelectedEditIndex(index);
    const address = userInfo.addresses[index];
    setValue('name', address.name);
    setValue('email', address.email);
    setValue('phone', address.phone);
    setValue('street', address.street);
    setValue('city', address.city);
    setValue('state', address.state);
    setValue('pinCode', address.pinCode);
  };

  const handleAdd = (address) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses, address] };
    dispatch(updateUserAsync(newUser));
    setShowAddAddressForm(false);
  };

  return (
    <div className="p-4 md:p-8 bg-gray-100">
      <div className="bg-white rounded-lg shadow-lg overflow-hidden">
        <div className="border-b border-gray-200 p-4 sm:p-6">
          <h1 className="text-2xl font-bold text-gray-900">
            Name: {userInfo.name ? userInfo.name : userInfo.email.substring(0, userInfo.email.lastIndexOf("@"))}
          </h1>
          <h3 className="text-lg font-semibold text-red-900">
            Email address: {userInfo.email}
          </h3>
          {userInfo.role === 'admin' && (
            <h3 className="text-lg font-semibold text-red-900">
              Role: {userInfo.role}
            </h3>
          )}
        </div>

        <div className="border-t border-gray-200 p-4 sm:p-6">
          <button
            onClick={() => {
              setShowAddAddressForm(true);
              setSelectedEditIndex(-1);
            }}
            type="button"
            className="w-full sm:w-auto bg-green-600 text-white font-semibold py-2 px-4 rounded-md shadow-sm hover:bg-green-500"
          >
            Add New Address
          </button>

          {showAddAddressForm && (
            <form
              className="bg-white p-4 sm:p-6 mt-4 rounded-lg shadow-sm"
              noValidate
              onSubmit={handleSubmit((data) => {
                handleAdd(data);
                reset();
              })}
            >
              <div className="space-y-6">
                <h2 className="text-xl font-semibold text-gray-900">
                  Personal Information
                </h2>
                <p className="text-sm text-gray-600">
                  Use a permanent address where you can receive mail.
                </p>
                <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
                  <div>
                    <label htmlFor="name" className="block text-sm font-medium text-gray-900">
                      Full name
                    </label>
                    <input
                      type="text"
                      {...register('name', { required: 'Name is required' })}
                      id="name"
                      className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                    />
                    {errors.name && <p className="text-red-500 text-sm">{errors.name.message}</p>}
                  </div>

                  <div>
                    <label htmlFor="email" className="block text-sm font-medium text-gray-900">
                      Email address
                    </label>
                    <input
                      id="email"
                      {...register('email', { required: 'Email is required' })}
                      type="email"
                      className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                    />
                    {errors.email && <p className="text-red-500 text-sm">{errors.email.message}</p>}
                  </div>

                  <div>
                    <label htmlFor="phone" className="block text-sm font-medium text-gray-900">
                      Phone
                    </label>
                    <input
                      id="phone"
                      {...register('phone', {
                        required: 'Phone is required',
                        pattern: { value: /^[0-9()-+ ]*$/, message: 'Invalid Phone Number' },
                      })}
                      type="tel"
                      className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                    />
                    {errors.phone && <p className="text-red-500 text-sm">{errors.phone.message}</p>}
                  </div>

                  <div className="col-span-full">
                    <label htmlFor="street" className="block text-sm font-medium text-gray-900">
                      Street address
                    </label>
                    <input
                      type="text"
                      {...register('street', { required: 'Street is required' })}
                      id="street"
                      className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                    />
                    {errors.street && <p className="text-red-500 text-sm">{errors.street.message}</p>}
                  </div>

                  <div>
                    <label htmlFor="city" className="block text-sm font-medium text-gray-900">
                      City
                    </label>
                    <input
                      type="text"
                      {...register('city', { required: 'City is required' })}
                      id="city"
                      className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                    />
                    {errors.city && <p className="text-red-500 text-sm">{errors.city.message}</p>}
                  </div>

                  <div>
                    <label htmlFor="state" className="block text-sm font-medium text-gray-900">
                      State / Province
                    </label>
                    <input
                      type="text"
                      {...register('state', { required: 'State is required' })}
                      id="state"
                      className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                    />
                    {errors.state && <p className="text-red-500 text-sm">{errors.state.message}</p>}
                  </div>

                  <div>
                    <label htmlFor="pinCode" className="block text-sm font-medium text-gray-900">
                      ZIP / Postal code
                    </label>
                    <input
                      type="text"
                      {...register('pinCode', {
                        required: 'Pin code is required',
                        pattern: { value: /^\d{6}$/, message: 'Pin code must be a 6-digit number' },
                      })}
                      id="pinCode"
                      className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                    />
                    {errors.pinCode && <p className="text-red-500 text-sm">{errors.pinCode.message}</p>}
                  </div>
                </div>

                <div className="flex justify-end gap-4">
                  <button
                    type="button"
                    onClick={() => setShowAddAddressForm(false)}
                    className="bg-gray-300 text-gray-700 font-semibold py-2 px-4 rounded-md shadow-sm"
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    className="bg-indigo-600 text-white font-semibold py-2 px-4 rounded-md shadow-sm"
                  >
                    Add Address
                  </button>
                </div>
              </div>
            </form>
          )}

          <p className="text-sm text-gray-500 mt-4">Your Addresses:</p>
          {userInfo.addresses.map((address, index) => (
            <div key={index} className="bg-white border border-gray-200 rounded-lg shadow-sm p-4 mb-4">
              {selectedEditIndex === index ? (
                <form
                  className="space-y-6"
                  noValidate
                  onSubmit={handleSubmit((data) => {
                    handleEdit(data, index);
                    reset();
                  })}
                >
                  <h2 className="text-xl font-semibold text-gray-900">
                    Personal Information
                  </h2>
                  <p className="text-sm text-gray-600">
                    Use a permanent address where you can receive mail.
                  </p>
                  <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
                    <div>
                      <label htmlFor="name" className="block text-sm font-medium text-gray-900">
                        Full name
                      </label>
                      <input
                        type="text"
                        {...register('name', { required: 'Name is required' })}
                        id="name"
                        className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                      />
                      {errors.name && <p className="text-red-500 text-sm">{errors.name.message}</p>}
                    </div>

                    <div>
                      <label htmlFor="email" className="block text-sm font-medium text-gray-900">
                        Email address
                      </label>
                      <input
                        id="email"
                        {...register('email', { required: 'Email is required' })}
                        type="email"
                        className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                      />
                      {errors.email && <p className="text-red-500 text-sm">{errors.email.message}</p>}
                    </div>

                    <div>
                      <label htmlFor="phone" className="block text-sm font-medium text-gray-900">
                        Phone
                      </label>
                      <input
                        id="phone"
                        {...register('phone', {
                          required: 'Phone is required',
                          pattern: { value: /^[0-9()-+ ]*$/, message: 'Invalid Phone Number' },
                        })}
                        type="tel"
                        className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                      />
                      {errors.phone && <p className="text-red-500 text-sm">{errors.phone.message}</p>}
                    </div>

                    <div className="col-span-full">
                      <label htmlFor="street" className="block text-sm font-medium text-gray-900">
                        Street address
                      </label>
                      <input
                        type="text"
                        {...register('street', { required: 'Street is required' })}
                        id="street"
                        className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                      />
                      {errors.street && <p className="text-red-500 text-sm">{errors.street.message}</p>}
                    </div>

                    <div>
                      <label htmlFor="city" className="block text-sm font-medium text-gray-900">
                        City
                      </label>
                      <input
                        type="text"
                        {...register('city', { required: 'City is required' })}
                        id="city"
                        className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                      />
                      {errors.city && <p className="text-red-500 text-sm">{errors.city.message}</p>}
                    </div>

                    <div>
                      <label htmlFor="state" className="block text-sm font-medium text-gray-900">
                        State / Province
                      </label>
                      <input
                        type="text"
                        {...register('state', { required: 'State is required' })}
                        id="state"
                        className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                      />
                      {errors.state && <p className="text-red-500 text-sm">{errors.state.message}</p>}
                    </div>

                    <div>
                      <label htmlFor="pinCode" className="block text-sm font-medium text-gray-900">
                        ZIP / Postal code
                      </label>
                      <input
                        type="text"
                        {...register('pinCode', {
                          required: 'Pin code is required',
                          pattern: { value: /^\d{6}$/, message: 'Pin code must be a 6-digit number' },
                        })}
                        id="pinCode"
                        className="w-full mt-1 px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                      />
                      {errors.pinCode && <p className="text-red-500 text-sm">{errors.pinCode.message}</p>}
                    </div>
                  </div>

                  <div className="flex justify-end gap-4">
                    <button
                      type="button"
                      onClick={() => setSelectedEditIndex(-1)}
                      className="bg-gray-300 text-gray-700 font-semibold py-2 px-4 rounded-md shadow-sm"
                    >
                      Cancel
                    </button>
                    <button
                      type="submit"
                      className="bg-indigo-600 text-white font-semibold py-2 px-4 rounded-md shadow-sm"
                    >
                      Save Changes
                    </button>
                  </div>
                </form>
              ) : (
                <div className="flex flex-col gap-4">
                  <div className="text-sm font-semibold text-gray-900">{address.name}</div>
                  <div className="text-xs text-gray-500 truncate">{address.street}</div>
                  <div className="text-xs text-gray-500 truncate">{address.city}, {address.state}, {address.pinCode}</div>
                  <div className="text-xs text-gray-500 truncate">{address.phone}</div>
                  <div className="flex gap-2">
                    <button
                      onClick={() => handleEditForm(index)}
                      type="button"
                      className="text-indigo-600 hover:text-indigo-500 text-sm"
                    >
                      Edit
                    </button>
                    <button
                      onClick={(e) => handleRemove(e, index)}
                      type="button"
                      className="text-red-600 hover:text-red-500 text-sm"
                    >
                      Remove
                    </button>
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

